import 'style/app.scss'
import data from 'mock/data'
import tabs from 'mock/tabs'
import AppMobile from 'AppMobile'
import InfoTabs from 'InfoTabs'

function App() {
  const toggleArea = (areaName, show = true) => {
    document
      .querySelectorAll(`.v-city .${areaName}`)
      .forEach((area) => area.classList.toggle('open', show))
  }

  return (
    <>
      <div className="v-city">
        <div className="v-map-wrapper">
          <img className="v-map" src="images/city.jpg" alt="city" />
          <div className="v-map-with-markers">
            <img src="images/_city-dev.jpg" alt="city with markers" />
          </div>

          <div className="v-areas">
            {data.map((area) => (
              <div
                className={`v-area ${area.name}`}
                key={area.name}
                onMouseEnter={() => toggleArea(area.name)}
                onMouseLeave={() => toggleArea(area.name, false)}
              ></div>
            ))}
          </div>

          <div className="v-markers">
            {data.map((area) => (
              <div
                className={`v-marker ${area.name}`}
                key={`marker-${area.name}`}
                onMouseEnter={() => toggleArea(area.name)}
                onMouseLeave={() => toggleArea(area.name, false)}
              >
                <img
                  src={`images/markers/${area.marker}.svg`}
                  alt={area.name}
                />
                <div className={`v-marker-tooltip ${area.position || 'left'}`}>
                  <div className="v-marker-tooltip-title">{area.title}</div>
                  <div
                    className="v-marker-tooltip-description"
                    dangerouslySetInnerHTML={{ __html: area.description }}
                  ></div>
                </div>
              </div>
            ))}
          </div>

          <div className="v-active-areas">
            {data.map((area, i) => {
              if ('active' in area)
                return (
                  <div className={`v-active-area ${area.name}`} key={i}>
                    <img
                      src={`images/active/${area.active}`}
                      alt={`active ${area.name}`}
                    />
                  </div>
                )
              return ''
            })}
          </div>

          <div className="v-elements">
            <div className="super-cashback">
              <img src="images/elements/mops.png" alt="Super Cashback" />
            </div>
            <div className="visa"></div>
            <div className="cashback">
              <img src="images/elements/cashback.png" alt="Cashback" />
            </div>
            <div className="online cart">
              <img src="images/elements/cart.png" alt="Cart" />
            </div>
            <div className="online serfer">
              <img src="images/elements/serfer.png" alt="Serfer" />
            </div>
            <div className="tv">
              <img src="images/elements/tv.svg" alt="TV" />
              <div className="tv-link">
                <div className="ty-link-text">Скоро буде...</div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="v-city-mobile">
        <AppMobile />
      </div>

      <InfoTabs data={tabs} />
    </>
  )
}

export default App
